<template>
    <p-card>
        <template #content>
            <p-data-table
                :value="users"
                showGridlines
                class="p-datatable-sm p-mb-1"
            >
                <template #header>
                    <div class="p-d-flex p-jc-between p-ai-center">
                        <h4 class="p-m-0">User Maintanace</h4>
                        <p-button @click="add" label="Add" icon="pi pi-plus" />
                    </div>
                </template>

                <p-column header="Name" field="name" />
                <p-column header="Username" field="username" />
                <p-column header="Email" field="email" />
                <p-column header="Phone" field="phone" />
                <p-column header="Acive" field="active" style="width: 120px">
                    <template #body="{data}">
                        <p-input-switch
                            v-model="data.active"
                            @click="updateStatus(data.id, !data.active)"
                        />
                    </template>
                </p-column>
                <p-column style="width: 120px">
                    <template #body="{data}">
                        <div class="p-d-flex p-jc-center">
                            <p-button
                                @click="edit(data.id)"
                                icon="pi pi-pencil"
                                class="p-mr-1"
                            />
                            <p-button
                                icon="pi pi-times"
                                class="p-button-danger"
                                @click="deleteUser(data.id)"
                            />
                        </div>
                    </template>
                </p-column>
            </p-data-table>
        </template>
    </p-card>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const toast = useToast();
        const router = useRouter();
        const store = useStore();
        const users = computed(() => store.state.users.users);

        const deleteUser = (id) => {
            store.dispatch("users/deleteUser", id).then(() => {
                location.reload();
            });
        };

        const updateStatus = (id, value) => {
            store
                .dispatch("users/updateUserStatus", {
                    id: id,
                    payload: { active: value }
                })
                .then(() => {
                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Update Successful",
                        life: 3000
                    });
                });
        };

        onMounted(() => {
            store.dispatch("users/getUsers");
        });

        return {
            users,
            add: () => {
                router.push({ path: "/users/create" });
            },
            edit: (id) => {
                router.push({ path: `/users/${id}/edit` });
            },
            deleteUser,
            updateStatus
        };
    }
};
</script>
